import React from 'react'
import upArrowIcon from '../../../../assets/img/UpIcon.png'
import downArrowIcon from '../../../../assets/img/DownIcon.png'
import bothArrowIcon from '../../../../assets/img/NoneIcon.png'

//      
const downIcon = downArrowIcon
const upIcon = upArrowIcon
const noneIcon = bothArrowIcon

export type SuperSortPropsType = {
    id?: string
    sort: string
    value: string
    onChange: (newSort: string) => void
}

export const pureChange = (sort: string, down: string, up: string) => {
    //  , sort: (click) => down (click) => up (click) => '' (click) => down ...
    if (sort === '') {
        return down
    } else if (sort === down) {
        return up
    } else if (sort === up) {
        return ''
    } else {
        return down
    }
    // 
}

const SuperSort: React.FC<SuperSortPropsType> = (
    {
        sort, value, onChange, id = 'hw15',
    }
) => {
    const up = '0' + value
    const down = '1' + value

    const onChangeCallback = () => {
        // @ts-ignore
        onChange(pureChange(sort, down, up))
    }

    const icon = sort === down
        ? downIcon
        : sort === up
            ? upIcon
            : noneIcon

    return (
        <span
            id={id + '-sort-' + value}
            onClick={onChangeCallback}
        >
            {/* */}
            <img
                id={id + '-icon-' + sort}
                src={icon}
                alt={'Arrow icon'}
            />

            {/*{icon} /!*  *!/*/}
        </span>
    )
}

export default SuperSort
